.modal-wrapper {
  position: fixed;
  top: 10px;
  left: 10px;
  right: 10px;
  bottom: 10px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  background-color: white;
  border-radius: 5px;
  padding: 50px;
  overflow: auto;
  display: flex;
  align-items: center;
  button {
    box-shadow: none;
    position: absolute;
    top: 10px;
    border: none;
    right: 10px;
    &:active {
      outline: none;
      box-shadow: none;
    }
    img {
      height: 15px;
    }
  }
  @media only screen and (max-width: 799px) {
    padding: 20px;
  }
}
